//   top: 0px;
//   left: 0px;
//   width: 100%;
// }
.navbar-nav {
  list-style: none;
  padding: 0px;
  margin: 0px;
  display: flex;
  align-items: center;
  height: 100%;
  li {
    margin-right: 30px;
    text-transform: uppercase;
    color: color("white");
    font-size: 16px;
    letter-spacing: 2px;

    a {
      @include link(color("white"), $value: 20);
    }
    &.active,
    a.active {
      font-weight: bold;
      color: color("primary");
    }
  }
}

footer.navbar {
  padding: 10px 0px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 99;
  ul.navbar-nav {
    justify-content: center;
    li {
      margin: 0px 20px;
      font-size: 16px;
      font-weight: normal;
      @include md {
        margin: 0px 10px;
        font-size: 14px;
      }
      @include sm {
        margin: 0px 5px;
        font-size: 12px;
      }
    }
  }
}
