
@mixin link($color: none, $darken: true, $value: 10){
    text-decoration: none;
    @if($color == none){
        color: inherit;
    } @else {
        color: $color;
        &:hover{
            @if($darken){
                color: darken($color, $value);   
            } @else {
                color: lighten($color, $value);   
            }
        }

    }
}

@mixin btn($bg: white, $color: black, $round: 5px, $padding: 15px 10px, $font: 24px) {
    border: none;
    cursor: pointer;
    background-color: $bg;
    color: $color;
    border-radius: $round;
    padding: $padding;
    font-size: $font;
    text-align: center;
    font-weight: bold;
    a{
        @include link;
    }
}

@mixin bg($url){
    background-image: url($url);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

@mixin heading($size: 64px, $margin: 80px){
    font-size: $size; 
    font-weight: bold;
    color: color('light');
    text-align: center;
    margin: 0px;
    margin-top: $margin;
    padding: 0px;
    margin: 0px;
    @include sm{
        font-size: 40px;
    }
}


@mixin img($height){
    width: 100%;
    max-height: $height;
    object-fit: cover;
    object-position: center;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
}

@mixin unstyle {
    list-style: none;
    padding: 0px;
    margin: 0px;
}