label {
  display: block;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: color("light");
  padding: 0px;
  margin: 0px;
  margin-bottom: 15px;
}

input:not([type*="file"]),
textarea {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  color: color("white");
  background-color: color("light-2");
  border: none;
  font-size: 20px;
  margin: 0px;
  resize: none;
  &::placeholder{
    color: color("light");
  }
}

input[type="file"] {
  display: none;
}

.file-placeholder {
  @include btn(color("light"), color("black"), 5px, 12px 0px, 20px);
  display: block;
  font-weight: bold;
  &:hover {
    background-color: darken(color("light"), 10);
  }
}

.form-group {
  margin-bottom: 30px;
}

.invalid-feedback,
.file-value {
  display: none;
  font-size: 14px;
  font-weight: normal;
  color: color("danger");
  margin-top: 10px;
}
.file-value {
  display: block;
  color: color('light');
}


.is-invalid {
  border: 2px solid color("danger") !important;
}
.is-invalid + .invalid-feedback,
.is-invalid + .file-value + .invalid-feedback {
display: block;
}

// Buttons
.button-group {
  display: flex;
  button {
    margin: 0px 15px;
  }
}

.btn {
  @include btn;
}

.search-form{
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  text-align: left;
  .form-group{
    margin: 0px;
  }
  label{
    text-transform: none;
  }
  button{
    @include btn($bg: color('light'), $color: color('black'), $round: 10px, $padding: 8px 0px, $font: 24px);
    display: block;
    width: 100%;
    margin: 0px;
    @include md{
      display: inline-block;
      width: auto;
      padding: 8px 20px;
      margin: 20px 0px;
    }
  }
}

.nav-form{
  position: relative;
  margin-top: 8px;
  input{
    background-color: color('dark');
    border-radius: 200px;
    font-size: 16px;
    padding: 5px 30px 5px 10px;
    outline: none;
    &::placeholder{
      color: color('black');
    }
  }
  .icon{
    height: 100%;
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
    font-size: 14px;
    border: 0px;
    background-color: color('light');
    margin: 0px;
    padding: 10px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    cursor: pointer;
    &:hover{
      background-color: darken(color('light'), 10);
    }
  }
}