* {
  box-sizing: border-box;
}

body {
  padding: 0px;
  margin: 0px;
  min-height: 100vh;
  position: relative;
  background-color: color("dark");
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}

.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.heading {
  @include heading();
}

.hero-section {
  height: 100vh;
  min-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  @include bg("/images/map.svg");
  @include md {
    background: none;
  }
  .overlay {
    background-color: color("dark");
    opacity: 0.96;
  }
  .content-wrapper {
    text-align: center;
    z-index: 2;
  }
  h1 {
    font-weight: normal;
    font-size: 64px;
    margin-bottom: 40px;
  }
  p {
    font-size: 24px;
    font-weight: normal;
    margin-bottom: 50px;
  }

  button {
    @include btn(color("primary"), color("white"), 5px, 10px 40px, 24px);
    font-weight: normal;
    &.btn-border {
      background-color: color("trans");
      border: 2px solid color("primary");
      color: color("light");
    }
    margin: 0px 15px;
  }

  button:hover {
    background-color: darken(color("primary"), 10px);
  }
  button.btn-border:hover {
    background-color: transparentize(color("primary"), 0.4);
    color: color("white");
  }
}

.features {
  display: flex;
  align-items: center;
  padding: 100px 0px;
  @include md {
    padding: 50px 0px;
  }

  .feature {
    text-align: center;
    color: color("light");
    @include md {
      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }
  }
  .feature-header {
    font-weight: normal;
    font-size: 24px;
    color: color("white");
    font-size: 24px;
    margin: 0px;
  }
  .feature-img {
    max-width: 80px;
    max-height: 80px;
    display: inline-block;
    margin: 30px auto;
  }
  .feature-text {
    padding: 0px;
    margin: 0px;
    font-size: 14px;
    line-height: 22px;
    color: color("light");
    text-align: center;
  }

  .feature:not(:nth-child(even)) {
    margin-top: 100px;
    @include md {
      margin-top: 0px;
    }
  }
}

.shapes{
  position: absolute;
  top: 0px;
  left: 0px;
  height: calc(100% - 70px);
  width: 100%;
  overflow: hidden;
  @include sm{
    display: none;
  }
  .shape{
    position: absolute;
  }

  @for $i from 1 through 22{
    // .shape:nth-child(#{$i}){
      // top: random($limit: 100) * 1%;
      // left: random($limit: 100) * 1%;
      // animation: rotate 10s linear infinite;
    // }

    @keyframes rotate {
      $dist: 20px;
      0%{transform: rotateZ(0deg) translate($dist, $dist);}
      50%{transform: rotateZ(180deg) translate(-$dist, -$dist);}
      100%{transform: rotateZ(360deg) translate($dist, $dist);}
    }

  }

}

#particles-js{
  position: absolute;
  top: 100px;
  left: 0px;
  width: 100%;
  @include md{
    visibility: hidden;
  }
}

// Create new event page section
.create-event {
  .heading {
    margin-top: 40px;
  }
}
.create-event-form {
  margin-top: 20px;
  margin-bottom: 100px;
  .map {
    height: 400px;
    width: 100%;
    margin-bottom: 50px;
  }
  button {
    @include btn(color("light"), color("black"), 5px, 10px 50px, 20px);
  }
}

.search-heading {
  @include heading($size: 32px);
  text-align: left;
  margin-bottom: 25px;
  @include md {
    font-size: 20px;
  }
}

// Event section
.event-section {
  padding-bottom: 100px;
  form{
    button{
      @include btn(color("light"), color("black"), 5px, 10px 20px, 18px);
      &:hover{
        background-color: darken(color('light'), 5);
      }
    }
  }
}


.timer {
  @include unstyle();
  display: flex;
  justify-content: center;
  margin: 50px 0px;
  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0px 10px;
  }
  .name {
    color: color("light-2");
    font-size: 18px;
    font-weight: bold;
    @include md {
      font-size: 14px;
    }
  }
  .time {
    color: color("primary");
    font-size: 64px;
    font-weight: bold;
    @include md {
      font-size: 32px;
    }
  }
}

.event-map {
  width: 100%;
  margin: 50px 0px;
  height: 500px;
}

.info {
  display: flex;
  justify-content: space-between;
  color: color("light-2");
  .date {
    font-size: 18px;
    font-weight: bolder;
  }
  .icons {
    font-size: 24px;
    font-weight: bold;
    .icon {
      display: inline-block;
      margin-left: 10px;
    }
  }
}

.event-img,
.event-img img {
  @include img(700px);
  display: block;
  width: auto;
  max-width: 100%;
  border-radius: 10px;
  overflow: hidden;
  margin: 25px auto;
  text-align: center;
}

.event-title {
  font-size: 32px;
  margin: 0px;
  margin-bottom: 25px;
  color: color("light");
  font-weight: bolder;
  @include md {
    font-size: 20px;
  }
}

.event-text {
  font-size: 18px;
  letter-spacing: 1px;
  line-height: 25px;
  margin-bottom: 20px;
  color: color("light");
  a {
    @include link(color("primary"));
    font-weight: bold;
  }
}

.btn-left {
  text-align: right;
  button {
    @include btn(
      $bg: color("light"),
      $color: color("black"),
      $round: 5px,
      $padding: 10px 24px,
      $font: 20px
    );
    &:hover {
      background-color: darken(color("light"), 10);
    }
  }
}

.comments {
  margin: 50px 0px;
}
.comment {
  background-color: color("dark-2");
  padding: 15px 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  .comment-header {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    color: color("light-2");
  }
  .comment-name {
    font-weight: bold;
    font-size: 18px;
    margin-right: 15px;
  }
  .comment-date {
    font-size: 12px;
  }
  .comment-text {
    font-size: 14px;
    color: color("light");
    padding: 0px;
    margin: 0px;
    a {
      @include link(color("violet"));
    }
  }
}

.page404 {
  margin: 50px 0px 0px 0px;
  text-align: center;
  padding-bottom: 50px;

  .heading {
    @include heading($size: 128px, $margin: 100px);
  }
  .sub-heading {
    @include heading();
    &.light {
      color: color("light");
    }
  }
  .heading,
  .sub-heading {
    color: color("light-2");
  }

  img{
      max-width: 200px;
      max-height: 200px;

      display: block;
      margin: 20px auto;
  }

}
