$colors: (
    'white': white,
    'black': black,
    'dark': #303240,
    'dark-2': #242634,
    'danger': #FF2264,
    'primary': #9CB7F6,
    'light': #ADABC9,
    'light-2': #5B6081,
    'violet': #8066FF,
    'trans': transparent,
);

@each $name, $value in $colors{
    // Text
    .text-#{$name}{
        color: $value;
    }
    // Background
    .bg-#{$name}{
        background-color: $value;
    }
}


// Function to get the color value from the map
@function color($color){
    @return map-get($colors,$color);
}