$space: (
    '10': 10px,
    '30': 30px
);

// Directions
$dir: (
    'l': 'left',
    'r': 'right',
    'b': 'bottom',
    't': 'top',
);

@each $name, $value in $space{
    // Padding
    @each $dname, $dvalue in $dir{
        .p#{$dname}-#{$name}{
            padding-#{$dvalue}: $value;
        }
        // Margin
        .m#{$dname}-#{$name}{
            margin-#{$dvalue}: $value;
        }
    }
}

.mx-auto{
    margin-left: auto;
    margin-right: auto;
}