.cards{
    .heading{
        margin-top: 30px;
        margin-bottom: 40px;
    }
    padding-bottom: 100px;
}
.card{
    background-color: color('dark-2');
    border-radius: 10px;
    margin-bottom: 30px;
    overflow: hidden;
    
    img.card-img,
    .card-img img{
        @include img(300px);
        display: block;
        width: auto;
        max-width: 100%;
    }

    .card-body{
        padding: 15px 10px;
    }
    .card-header{
        padding: 0px;
        margin: 0px;
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 10px;
        color: color('primary');
        a{
            @include link(color('primary'));
        }
    }
    .card-text{
        color: color('light');
        font-size: 16px;
        margin: 0px;
        padding: 0px;
        margin-bottom: 15px;
        line-height: 22px;
        @include md{
            font-size: 14px;
        }
    }
    .card-footer{
        display: flex;
        justify-content: space-between;
    }
    .card-location{
        color: color('primary');
        font-weight: bold;
        font-size: 14px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 15px;
        max-width: calc(100% - 120px);
        a{
            @include link(color('primary'));
        }
    }
    .card-icon{
        color: color('light-2');
        font-size: 12px;
        font-weight: bold;
        margin-left: 10px;
        display: inline-block;
    }

    
}