// Grid variables
$numberOfColumns: 12;
$gutter: 30px;

$break-points: (
    'sm': 800px,
    'md': 1100px,
);

// Breakpoints
@mixin sm(){
    @media screen and (max-width: 800px){
        @content;
    }
}
@mixin md(){
    @media screen and (max-width: 1100px){
        @content;
    }
}



.container{
    padding-right: 105px;
    padding-left: 105px;
    margin-right: 0px;
    margin-left: 0px;
    @include sm{
        padding-right: 10px;
        padding-left: 10px;
    }
}

.row{
    display: flex;
    flex-wrap: wrap;
}

[class*="col"]{
    padding-right: $gutter / 2;
    padding-left: $gutter / 2;
}
.col{
    flex-grow: 1;
}

// Creating grid columns classes
@for $i from 1 through $numberOfColumns{
    .col-#{$i}{
        width: (100% / 12) * $i;
    }

    // Breakpoints
    .col-md-#{$i}{
        @include md{
            width: (100% / 12) * $i;
        }
    }
    .col-sm-#{$i}{
        @include sm{
            width: (100% / 12) * $i;
        }
    }


}