.pagination{
    width: 100%;
    list-style: none;
    padding: 0px;
    margin: 50px 0px 30px 0px;
    display: flex;
    justify-content: center;
    li{
        border-radius: 3px;
        padding: 5px;
        margin: 0px 5px;
        background-color: color('light-2');
        color: color('white');
        font-weight: bold;
        a{
            @include link;
        }
    }
    li.active,
    a.active{
        background-color: color('primary');
    }
}